import Bubble from './bubble';

class Shooter {
  constructor(canvas, ctx){
    this.loadedBubbles = [];
    this.canvas = canvas;
    this.ctx = ctx;
    this.height = 50;
    this.width = 3;
  }

  loadBubbles(){
    if (this.loadedBubbles.length !== 2) {
      if (this.loadedBubbles.length === 0) {
        this.loadedBubbles.push(new Bubble(this.canvas.width/2 + 1,this.canvas.height - 10, this.ctx));
      } else {
        this.loadedBubbles.push(new Bubble(this.canvas.width/4, this.canvas.height - 10, this.ctx));
      }
      this.loadBubbles();
    }
  }

  drawShooter(){
    this.ctx.lineWidth = 2;
    this.ctx.strokeStyle = "#0000ff";
    this.ctx.beginPath();
    this.ctx.moveTo(centerx, centery);
    this.ctx.lineTo(centerx + 1.5*level.tilewidth * Math.cos(degToRad(player.angle)), centery - 1.5*level.tileheight * Math.sin(degToRad(player.angle)));
    this.ctx.stroke();
    // this.ctx.beginPath();
    // this.ctx.rect(
    //   this.canvas.width/2,
    //   this.canvas.height - 60,
    //   this.width, this.height
    // );
  }

  drawLoadedBubbles(){
    this.ctx.fillStyle = "#000000";
    this.ctx.fill();
    this.ctx.closePath();
    this.loadBubbles();
    this.loadedBubbles[0].drawBubble();
    this.loadedBubbles[1].drawBubble();
  }
}

export default Shooter;



